import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import Home from '../static/js/home/Home';
import Main from '../static/js/main/Main';
import Artist from '../static/js/artist/Artist';
import Video from '../static/js/video/Video';
import Shop from '../static/js/shop/Shop';
import Contact from '../static/js/contact/Contact';


class App extends Component {
  render() {
    return (
      <div className="App">
        <Route exact path="/" component={Home}/>
        <Route path="/index" component={Main}/>
        <Route path="/artist" component={Artist}/>
        <Route path="/Video" component={Video}/>
        <Route path="/Shop" component={Shop}/>
        <Route path="/Contact" component={Contact}/>
      </div>
    );
  }
}

export default App;