import React, {Component} from 'react';
import Carousel from 'react-bootstrap/Carousel'
import MainPicture from '../common/MainPicture';
import MainMenu from '../common/MainMenu';
import '../../css/artist/Artist.css';
import artist1 from'../../img/artist/artist1.jpg';
import artist2 from'../../img/artist/artist2.jpg';
import artist3 from'../../img/artist/artist3.jpg';
import artist4 from'../../img/artist/artist4.jpg';

class ArtistPicture extends Component {
    constructor(props, context) {
      super(props, context);
  
      this.handleSelect = this.handleSelect.bind(this);
  
      this.state = {
        index: 0,
        direction: null,
      };
    }
  
    handleSelect(selectedIndex, e) {
      this.setState({
        index: selectedIndex,
        direction: e.direction,
        fade: true
      });
    }
  
    render() {
      const { index, direction, fade } = this.state;
  
      return (
        <Carousel
          activeIndex={index}
          direction={direction}
          onSelect={this.handleSelect}
          fade={fade}
        >
          <Carousel.Item>
            <img
              className="memberImg d-block w-100"
              src={artist1}
              alt="First slide"
            />
            <Carousel.Caption>
              <h3>Bkkoli</h3>
              <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="memberImg d-block w-100"
              src={artist2}
              alt="Second slide"
            />
            <Carousel.Caption>
              <h3>SEOWOO</h3>
              <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="memberImg d-block w-100"
              src={artist3}
              alt="Third slide"
              />
            <Carousel.Caption>
              <h3>C-in</h3>
              <p>
                Praesent commodo cursus magna, vel scelerisque nisl consectetur.
              </p>
            </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
            <img
              className="memberImg d-block w-100"
              src={artist4}
              alt="Four slide"
            />
            <Carousel.Caption>
              <h3>kidzoo</h3>
              <p>
                Praesent commodo cursus magna, vel scelerisque nisl consectetur.
              </p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="memberImg d-block w-100"
              src={artist4}
              alt="Five slide"
            />
            <Carousel.Caption>
              <h3>D.A</h3>
              <p>
                Praesent commodo cursus magna, vel scelerisque nisl consectetur.
              </p>
            </Carousel.Caption>
          </Carousel.Item>
        </Carousel>
      );
    }
  }

class Artist extends Component {

    render () {
        return (
            <>
                <ArtistPicture/>
                <MainMenu/>
            </>
        );
    }
}

export default Artist;